const raycaster = new THREE.Raycaster();
let mouse = new THREE.Vector2();
let destinationPoint = new THREE.Vector3();
let angle = 0;
let isPressed = false; //    

// 
const clock = new THREE.Clock();

export default function draw() {
	const delta = clock.getDelta();

	if (player.mixer) player.mixer.update(delta);

	if (player.modelMesh) {
		camera.lookAt(player.modelMesh.position);
	}

	if (player.modelMesh) {

		if (isPressed) {
			raycasting();
		}

		if (player.moving) {
			//  
			angle = Math.atan2(
				destinationPoint.z - player.modelMesh.position.z,
				destinationPoint.x - player.modelMesh.position.x
			);
			player.modelMesh.position.x += Math.cos(angle) * 0.05;
			player.modelMesh.position.z += Math.sin(angle) * 0.05;

			camera.position.x = cameraPosition.x + player.modelMesh.position.x;
			camera.position.z = cameraPosition.z + player.modelMesh.position.z;
			
			player.actions[0].stop();
			player.actions[1].play();
			
			if (
				Math.abs(destinationPoint.x - player.modelMesh.position.x) < 0.03 &&
				Math.abs(destinationPoint.z - player.modelMesh.position.z) < 0.03
			) {
				player.moving = false;
				console.log('');
			}

			if (
				Math.abs(spotMesh.position.x - player.modelMesh.position.x) < 1.5 &&
				Math.abs(spotMesh.position.z - player.modelMesh.position.z) < 1.5
			) {
				if (!house.visible) {
					console.log('');
					house.visible = true;
					spotMesh.material.color.set('seagreen');
					gsap.to(
						house.modelMesh.position,
						{
							duration: 1,
							y: 4.5,
							ease: 'Bounce.easeOut'
						}
					);
					gsap.to(
						camera.position,
						{
							duration: 1,
							y: 3
						}
					);

					$('.btn-view').fadeIn(300);
				}
			} else if (house.visible) {
				console.log('');
				house.visible = false;
				spotMesh.material.color.set('yellow');
				gsap.to(
					house.modelMesh.position,
					{
						duration: 0.5,
						y: -0.15
					}
				);
				gsap.to(
					camera.position,
					{
						duration: 1,
						y: 2.5
					}
				);

				$('.btn-view').fadeOut(300);
			}
		} else {
			//   
			player.actions[1].stop();
			player.actions[0].play();
		}
	}

	renderer.render(scene, camera);
	renderer.setAnimationLoop(draw);



}

function checkIntersects() {
	// raycaster.setFromCamera(mouse, camera);

	const intersects = raycaster.intersectObjects(meshes);
	for (const item of intersects) {
		if (item.object.name === 'floor') {
			destinationPoint.x = item.point.x;
			destinationPoint.y = 0.3;
			destinationPoint.z = item.point.z;
			player.modelMesh.lookAt(destinationPoint);

			// console.log(item.point)

			player.moving = true;

			pointerMesh.position.x = destinationPoint.x;
			pointerMesh.position.z = destinationPoint.z;
		}
		break;
	}

}

function setSize() {
	camera.left = -(window.innerWidth / window.innerHeight);
	camera.right = window.innerWidth / window.innerHeight;
	camera.top = 1;
	camera.bottom = -1;

	camera.updateProjectionMatrix();
	renderer.setSize(window.innerWidth, window.innerHeight);
	renderer.render(scene, camera);
}

// 
window.addEventListener('resize', setSize);

//   three.js  
function calculateMousePosition(e) {
	mouse.x = e.clientX / canvas.clientWidth * 2 - 1;
	mouse.y = -(e.clientY / canvas.clientHeight * 2 - 1);
}

//     
function raycasting() {
	raycaster.setFromCamera(mouse, camera);
	checkIntersects();
}

//  
canvas.addEventListener('mousedown', e => {
	isPressed = true;
	calculateMousePosition(e);
});
canvas.addEventListener('mouseup', () => {
	isPressed = false;
});
canvas.addEventListener('mousemove', e => {
	if (isPressed) {
		calculateMousePosition(e);
	}
});

//  
canvas.addEventListener('touchstart', e => {
	isPressed = true;
	calculateMousePosition(e.touches[0]);
});
canvas.addEventListener('touchend', () => {
	isPressed = false;
});
canvas.addEventListener('touchmove', e => {
	if (isPressed) {
		calculateMousePosition(e.touches[0]);
	}
});





$(document).on('click', '.btn-reset', function(){
	// window.location.reload();
	
	player.modelMesh.position.set(0, 0.3, 0);
	player.modelMesh.rotation.y = 0;
	destinationPoint.set(0, 0.3, 0);
	camera.position.set(1, 2.5, 5);
	pointerMesh.position.set(0, 0.1, 0.3);

	house.visible = false;
	spotMesh.material.color.set('yellow');
	gsap.to(
		house.modelMesh.position,
		{
			duration: 0.5,
			y: -0.15
		}
	);

	$('.btn-view').hide();
	

})



draw();


