import logo from '../assets/images/logo.svg';
import { Component } from 'react';


class Header extends Component {
  render() {
    return (
      <header id="header">
        <a href="/portfolio-v2" className="header-logo" title=" ">
          <img src={logo} alt=" " />
        </a>

        <h1>{this.props.title}</h1>
        
      </header>
    )
  }
}

export default Header;