import React from 'react';
import Header from './Header';
import '../assets/css/normalize.css';
import '../assets/css/fonts.css';
import '../assets/scss/layout.scss';


//   header   header 
const Layout = ({header, children}) => {
  return (
    <div id="wrapper">
      {header && (
        <Header
          title={header.title}/>
      )}
      <div id="container">
        {children}
      </div>
    </div>
  );
}

export default Layout;